<template>
  <div class="comment-post">
    <van-field
      class="post-field"
      v-model.trim="message"
      rows="2"
      autosize
      type="textarea"
      maxlength="50"
      placeholder="优质评论将被优先显示"
      show-word-limit
    />

    <van-button
      class="post-btn"
      size="small"
      @click="onAddComment"
      :disabled="!message.length"
    >
      发布
    </van-button>
  </div>
</template>

<script>
import { addCommentAPI } from '../../../api/index.js'
export default {
  name: 'CommentPost',
  props: {
    targetId: { // 文章id 或 评论id
      type: String,
      default: ''
    },
    articleId: { // 如果是回复评论，则需要传文章id进来
      type: String,
      default: ''
    }
  },

  inject: {
    articleIdPro: {
      type: String,
      default: ''
    }
  },

  mounted () {
    console.log(this.articleIdPro, 'post-inject')
  },

  data () {
    return {
      message: ''
    }
  },
  // provide inject
  methods: {
    async onAddComment () {
      console.log(this.articleId)
      try {
        const messageTemp = this.message.trim()
        // 非空判断
        if (!messageTemp.length) return

        const { data: res } = await addCommentAPI({
          target: this.targetId, // 文章id 或 评论id
          content: this.message, // 频率内容
          art_id: this.articleId || null // 若 target 是 评论id时， 该属性为 文章id
          /*
            问题：当我们回复评论时，没有去添加改id，但是评论回复列表中依然可以添加成功。
            => 1. 后端的代码并不严谨 2. 后端提供的接口文档中这个art_id是一个无用参数
          */
        })

        console.log(res)
        // 发布成功后，情况输入框内容
        this.message = ''
        // 关闭弹出层
        this.$emit('post-success', res.data)
        this.$toast.success('发布成功')
      } catch (error) {
        this.$toast.fail('发布失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.comment-post {
  display: flex;
  align-items: center;
  padding: 32px 0 32px 32px;

  .post-field {
    background-color: #f5f6f7;
  }

  .post-btn {
    width: 150px;
    border: none;
    padding: 0;
    color: #6ba3d8;
  }
}
</style>
