<template>
<!-- 文章详情 - 这个组件以后是只能做路由跳转吗？他可不可能作为某个父组件的子组件嵌套展示在页面上？ -->
<!-- 能否通过$route的形式作为子组件时获取 - 答：不能 -->
<!-- 有没有可能 路由传参和 组件传参都用一种获取参数的方式来执行呢? 答: 开启props传参 -->
  <div class="article-container">
    <!-- 文章详情 标题 fixed将头部导航脱离文档流显示到最顶端 -->
    <van-nav-bar
      title="黑马头条"
      left-arrow
      @click-left="$router.back()"
      fixed
    />

    <!-- 正文滚动区域 -->
    <div class="main-warp">
      <!-- 加载中 -->
      <div class="loading-wrap" v-if="loading">
        <van-loading color="#1989fa"  vertical>加载中。。。</van-loading>
      </div>

      <!-- 文章详情部分 -->
      <div class="article-detail" v-else-if="article.title">
        <!-- 文章标题 -->
        <h1 class="article-title">{{ article.title }}</h1>

        <!-- 用户信息 -->
        <van-cell class="user-info">
          <!-- 用户头像 -->
          <template #icon>
            <van-image
            class="avatar"
              round
              fit="cover"
              :src="article.aut_photo"
            />
          </template>

          <!-- 用户名 -->
          <template #title>
            <div class="user-name">{{ article.aut_name }}</div>
          </template>

          <!-- 相对时间 -->
          <template #label>
            <div class="publish-date">{{ article.pubdate | relativeTime }}</div>
          </template>

          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon>
            <!-- 通过loading属性，对按钮进行节流操作 -->
            <!-- <van-button v-if="article.is_followed" class="follow-btn" round type="info" size="small" icon="plus" @click="onFollow" :loading="btnLoading">关注</van-button>
            <van-button v-else class="follow-btn" round size="small" @click="onFollow" :loading="btnLoading">已关注</van-button> -->
            <follow-user
              :userId="article.aut_id"
              :isFollowed.sync="article.is_followed"
            ></follow-user>
            <!--  v-model=""的作用，双向绑定数据源
                  :isFollowed="article.is_followed"
                  v-model="article.is_followed"
            -->
          </template>
        </van-cell>

        <!-- 文章内容 -->
        <div ref="article-content" class="article-content markdown-body" v-html="article.content"></div>
        <van-divider>正文结束</van-divider>

        <!-- 文章评论 -->
        <!-- 为什么使用articleId 而不是 article.art_id
            原因：article需要通过接口来获取数据，但是刚进入页码是就出发了comment-list组件中的onload方法，此时没有article.art_id存在所以请求不到内容
        -->
        <comment-list
          ref="comment-list"
          :targetId="articleId"
          :totalCount.sync="totalCommentCount"
          @reply-click="onReplyClick"
        ></comment-list>
      </div>
      <!-- 文章详情部分 - end -->

      <!-- 加载失败 - 资源不存在 404 -->
      <div class="error-wrap" v-else-if="errStatus === 404">
        <van-icon name="failure" />
        <p class="text">该资源不存在或已删除！</p>
      </div>

      <!-- 加载失败 - 其他内容 可点击重试 -->
      <div class="error-wrap" v-else>
        <van-icon name="failure" />
        <p class="text">内容加载失败！</p>
        <!-- 直接调用获取详情接口的方法 -->
        <van-button class="retry-btn" @click="loadArticleInfo">点击重试</van-button>
      </div>

    </div>
    <!-- 正文滚动结束 -->

    <!-- 底部区域 -->
    <div class="article-bottom">
      <van-button class="comment-btn" round size="small" @click="isPostShow = true">写评论</van-button>
      <!-- 评论标记 -->
      <van-icon name="comment-o" :badge="totalCommentCount"/>
      <!-- 收藏 -->
      <!-- <van-icon name="star-o" /> -->
      <collect-article
        :articleId="article.art_id"
        v-model="article.is_collected"
      ></collect-article>
      <!-- 点赞 -->
      <!-- <like-articlevan-icon name="good-job-o" /> -->
      <!-- .sync 方法3来进行数据传递 -->
      <like-article
        :articleId="article.art_id"
        :attitude.sync="article.attitude"
      ></like-article>
      <!-- 转发 -->
      <van-icon name="share" />
    </div>

    <!-- 文章评论 -->
    <van-popup v-model="isPostShow" position="bottom">
      <comment-post :targetId="article.aut_id" @post-success="onPostSuccess"></comment-post>
    </van-popup>

    <!-- 评论详情 - 回复评论 及 查看回复列表 -->
    <van-popup
      v-model="isReplyShow"
      position="bottom"
      :style="{ height: '100%' }"
    >
    <!--
      发现了一个问题： 在切换不同的评论弹出层时， 发现文章评论详情发生了变化，但是评论回复列表区域还是上一条的列表
      van-popup弹出层组件，v-model特点
        初始状态下，v-model状态为false 弹出层内容不渲染
        当v-model变成true时， 弹出层渲染数据
        当在此关闭弹出层时，只是对css进行了控制和隐藏

        解决方案： 我们需要给评论回复弹出层组件添加一个v-if，来保证当弹出层关闭时，能够清除内部的所有dom元素，保证下次打开后回复列表可以正常请求列表接口
     -->
      <comment-reply v-if="isReplyShow" :comment="currentComment" @close="isReplyShow = false"></comment-reply>
    </van-popup>
  </div>
</template>

<script>
import FollowUser from '../../components/follow-user.vue'
import CollectArticle from '../../components/collect-article.vue'
import LikeArticle from '../../components/like-article.vue'
import CommentList from './components/comment-list.vue'
import CommentPost from './components/comment-post.vue'
import CommentReply from './components/comment-reply.vue'
import { getArticleByIdAPI } from '../../api/index.js'
import { ImagePreview } from 'vant'
export default {
  name: 'articleIndex',
  components: {
    FollowUser,
    CollectArticle,
    LikeArticle,
    CommentPost,
    CommentList,
    CommentReply
  },
  props: {
    articleId: {
      type: String,
      default: ''
    }
  },

  /*
    在vue项目中，往往会涉及到多层组件的嵌套传参，或者是一个参数需要传递给多个组件
    vue提供给了一种父组件传递数据给它所包含的所有子(后代)组件的方式
    provide 一个函数（方法）父组件通过该函数将数据发送
    inject 在子组件中，作为一个属性（该属性的写法与props完全相同），可以接受到provide传递过来的数据

    注意事项： 如果provide传递的是一个引用数据类型，每个组件都可以修改它堆内存中的数据，但如果对该数据进行了修改，后期很难查找到修改源，所以在使用这个provide和inject进行数据传递时，一定遵守只读原则
    如果说，自己不能保证你使用的数据不会被修改，请使用最基本的组件传参形式
  */
  provide: function () {
    return {
      articleIdPro: this.articleId
    }
  },

  data () {
    return {
      article: {}, // 文章详情
      loading: true, // 加载中状态，页面渲染时自动开启，当接口请求完成时关闭该状态
      errStatus: 0, // 如果发送报错，通过该变量来判断显示哪一个报错信息
      totalCommentCount: 0, // 评论区评论总数
      isPostShow: false, // 发布评论显示状态
      isReplyShow: false, // 评论回复列表状态
      currentComment: {} // 当前点击的评论内容 - 需要传给回复评论弹出层
      // btnLoading: false // 关注按钮加载状态
    }
  },
  created () {
    // 获取文章详情
    this.loadArticleInfo()
  },
  methods: {
    async loadArticleInfo () {
      this.loading = true // 请求前开启加载
      try {
        // const { data: res } = await getArticleByIdAPI(this.articleId)
        const { data: res } = await getArticleByIdAPI(7817)

        this.article = res.data

        // 请求数据成功，触发制作图片列表事件 - 操作dom元素 - 异步操作
        setTimeout(() => {
          this.previewImage()
        }, 0)
      } catch (error) {
        // error.response.status 这是错误请求中的固定参数 - 错误编码code
        if (error.response && error.response.status === 404) {
          this.errStatus = 404
        }
        this.$toast('获取失败')
      }

      // 无论成功还是失败，都需要关闭加载状态
      this.loading = false
    },

    /*
    多图预览 代码解释
    ImagePreview({
      images: [ // 预览的图片合集，可以切换的张数
        'https://img01.yzcdn.cn/vant/apple-1.jpg',
        'https://img01.yzcdn.cn/vant/apple-2.jpg',
      ],
      startPosition: 1, // 当前第一次打开是的图片索引，从0开始计数
      onClose () {} // 当退出大图预览时触发的事件方法
    });
    */
    previewImage () {
      // 1. 得到正文中所有的图片元素
      // document.querySelectorAll('img') // body开始找所有的img标签
      const articleContent = this.$refs['article-content'] // 先找到父元素对象
      // 兼容性配置， 如果$refs['article-content']元素找不到，那么终止后面代码的执行
      if (!articleContent) return
      const images = articleContent.querySelectorAll('img')
      // images 是伪数组
      // 2. 创建一个处理后只有图片路径的真数组 - 给ImagePreview.images属性使用
      /*
        [...images] 将伪数组转化成真数组
        .map(img => img.src) 将数组中的每一项变成url字符串
      */
      const imageList = [...images].map(img => img.src)

      // 3. 给每一张图片绑定打开图片预览功能的点击事件
      images.forEach((img, index) => {
        img.onclick = () => {
          ImagePreview({
            images: imageList, // 预览的图片合集，可以切换的张数
            startPosition: index // 当前第一次打开是的图片索引，从0开始计数
          })
        }
      })
    },

    // 文章评论弹出层关闭操作
    onPostSuccess (item) {
      this.isPostShow = false

      // 通过ref去调用comment-list的方法将新的评价添加到列表的最顶端
      this.$refs['comment-list'].unshiftComment(item)
      console.log(item)
    },

    // 开启评论回复弹出层
    onReplyClick (comment) {
      // 赋值当前文章评论对象
      this.currentComment = comment || {}
      console.log(comment)
      // 显示回复弹出层
      this.isReplyShow = true
    }

    // // 取关、关注用户
    // async onFollow () {
    //   this.btnLoading = true // 请求接口前开启
    //   try {
    //     // 通过当前的用户关注状态来判断调用那个接口
    //     if (this.article.is_followed) {
    //       // 已关注状态 - 要取消关注
    //       await deleteFollowAPI(this.article.aut_id)
    //     } else {
    //       // 未关注状态 - 要开启关注
    //       await addFollowAPI(this.article.aut_id)
    //     }

    //     // 主动的将状态取反
    //     this.article.is_followed = !this.article.is_followed
    //   } catch (error) {
    //     console.log(error)
    //     if (error.response && error.response.status === 400) {
    //       this.$toast('你不能关注你自己')
    //     } else {
    //       this.$toast('操作失败，请重试')
    //     }
    //   }
    //   setTimeout(() => {
    //     this.btnLoading = false // 请求执行后关闭
    //   }, 1000)
    // }
  }
}
</script>

<style lang="less" scoped>
@import './css/github-markdown.css';

.article-container {
  .main-warp {
    padding-top: 92px;
    padding-bottom: 88px;
    overflow-y: auto;
    background-color: #fff;
  }

  // 加载中样式表
  .loading-wrap {
    padding: 200px 32px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  // 正文部分
  .article-detail {
    .article-title {
      font-size: 40px;
      padding: 50px 32px;
      margin: 0;
      color: #3a3a3a;
    }

    .user-info {
      padding: 0 32px;
      .avatar {
        width: 70px;
        height: 70px;
        margin-right: 12px;
      }

      .user-name {
        font-size: 24px;
        color: #3a3a3a;
      }

      .van-cell__label {
        margin: 0;

        .publish-date {
          font-size: 24px;
          color: #b7b7b7;
        }
      }

      .follow-btn {
        width: 180px;
        height: 58px;
      }
    }

    .article-content {
      padding: 50px 32px;
    }
  }

  .error-wrap {
    padding: 200px 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .van-icon {
      font-size: 130px;
      color: #b4b4b4;
    }

    .text {
      font-size: 34px;
      color: #666;
      margin: 32px 0 64px;
    }

    .retry-btn {
      width: 280px;
      height: 70px;
      line-height: 70px;
      border: 1px solid #c3c3c3;
      font-size: 34px;
      color: #666;
    }
  }

  // 底部区域样式
  .article-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 88px;
    border-top: 1px solid #d8d8d8;
    background-color: #fff;

    .comment-btn {
      width: 280px;
      height: 46px;
      line-height: 46px;
      border: 2px solid #eeeeee;
      color: #a7a7a7;
    }

    .van-icon {
      font-size: 40px;
      .van-info {
        font-size: 16px;
        background-color: #e22829;
      }
    }
  }
}

</style>
