<template>
    <!-- 通过loading属性，对按钮进行节流操作 -->
    <!-- 我们说过组件的跟节点只能有一个，但是虽然下面写了2个根节点，但是因为有v-if 和 v-else的逻辑存在，导致整个业务中不可能下面的两个节点同时存在 -->
  <van-button v-if="isFollowed" class="follow-btn" round type="info" size="small" icon="plus" @click="onFollow" :loading="btnLoading">关注  {{ isFollowed }}</van-button>

  <van-button v-else class="follow-btn" round size="small" @click="onFollow" :loading="btnLoading">已关注 {{ isFollowed }}</van-button>
</template>

<script>
import { addFollowAPI, deleteFollowAPI } from '../api/index.js'
export default {
  name: 'FollowUser',
  /*
    如果用v-model传递过来的参数，默认情况下，props使用 value 来进行接收的
    通过model属性来自定义value的接收方式
    通过v-model绑定的变量，有一个默认的事件 input事件。可以通过该事件来实现双向绑定数据的更新
    通过model属性来自定义input事件的名称
  */
  model: {
    prop: 'isFollowed', // 默认为value
    event: 'update-follow' // 默认为input
  },
  props: {
    isFollowed: { // article.is_followed
      type: Boolean,
      default: true
    },
    userId: { // article.aut_id 用户id
      type: String,
      default: ''
    }
  },
  data () {
    return {
      btnLoading: false // 调用接口时的加载状态
    }
  },
  methods: {
    // 取关、关注用户
    async onFollow () {
      this.btnLoading = true // 请求接口前开启
      try {
        // 通过当前的用户关注状态来判断调用那个接口
        if (this.isFollowed) {
          // 已关注状态 - 要取消关注
          await deleteFollowAPI(this.userId)
        } else {
          // 未关注状态 - 要开启关注
          await addFollowAPI(this.userId)
        }

        // 主动的将状态取反
        // this.isFollowed = !this.isFollowed
        this.$emit('update:isFollowed', !this.isFollowed)
        // @input="article.is_followed = $event"
      } catch (error) {
        console.log(error)
        if (error.response && error.response.status === 400) {
          this.$toast('你不能关注你自己')
        } else {
          this.$toast('操作失败，请重试')
        }
      }
      setTimeout(() => {
        this.btnLoading = false // 请求执行后关闭
      }, 1000)
    }
  }
}

/*
  父组件
    方法一
    <子组件 :key1="item1"> => 发送
    <子组件 :key1="item1" @ourFn> => 被子组件触发该自定义事件实现item1变量的修改

    方法二
    <子组件 v-model="item2"> => 发送

    方法三
    <子组件 :key3.sync="item3"> => 发送
  子组件
    方法一
    props: { item1: {} } => 接收
    this.$emit('ourFn', 参数) => 修改item1的方式

    方法二
    默认接收方式： props: { value: {} } => 接收
    自定义接收方式: model: { prop: '自定义接收的变量名' }

    默认修改该变量的事件: input, this.$emit('input', 需要修改的值)
    自定义事件名: model: { event: '自定义事件名' }

    方法三
    props: { key3: {} } => 接收
    this.$emit('update:key3', 需要修改的值)

    方法二、方法三 只能改变当前绑定变量的值，不能够做其他的业务操作
*/
</script>

