<template>
  <div>
    <van-icon
      :name="value ? 'star' : 'star-o'"
      :class="{collected: value}"
      :loading="btnLoading"
      @click="onCollect"
    />

  <!-- <van-button v-else class="follow-btn" round size="small" @click="onFollow" :loading="btnLoading">已关注 {{ isFollowed }}</van-button> -->

  </div>
</template>

<script>
import { addCollectAPI, deleteCollectAPI } from '../api/index.js'
export default {
  name: 'CollectArticle',
  // 使用的是v-model的形式来进行参数的传递
  props: {
    value: { // article.is_collected 收藏状态
      type: Boolean,
      default: true
    },
    articleId: { // article.art_id 文章id
      type: String,
      default: ''
    }
  },
  data () {
    return {
      btnLoading: false // 调用接口时的加载状态
    }
  },
  methods: {
    // 取消 、 收藏文章
    async onCollect () {
      this.btnLoading = true // 请求接口前开启
      try {
        // 通过当前的用户关注状态来判断调用那个接口
        if (this.value) {
          // 已关注状态 - 要取消关注
          await deleteCollectAPI(this.articleId)
        } else {
          // 未关注状态 - 要开启关注
          await addCollectAPI(this.articleId)
        }

        // 主动的将状态取反
        this.$emit('input', !this.value)
      } catch (error) {
        console.log(error)
        if (error.response && error.response.status === 400) {
          this.$toast('你不能关注你自己')
        } else {
          this.$toast('操作失败，请重试')
        }
      }
      setTimeout(() => {
        this.btnLoading = false // 请求执行后关闭
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.collected {
  color: #ffa500;
}
</style>
