<template>
  <div class="comment-list">
    <!-- 文章评论列表 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
      @load="onLoad"
    >
      <comment-item
        v-for="(commentItem, index) in list"
        :key="index"
        :comment="commentItem"
        @reply-click="onReplyClick"
      ></comment-item>
      <!--  @reply-click="$`('reply-click', $event)"
             $event === comment
      -->
    </van-list>
  </div>
</template>

<script>
import CommentItem from './comment-item.vue'
import { getCommentsAPI } from '../../../api/index.js'
export default {
  name: 'CommentList',
  components: {
    CommentItem
  },
  props: {
    targetId: { // 父组件传入的id, 可能是文章id也可能是评论id
      type: String,
      default: ''
    },
    totalCount: {
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: 'a'
    }
  },
  /*
    查找组件中使用数据的不同点
    顺着组件与组件，方法与方法的联系一步一步的找到根源，在此过程中考虑每一个细节是否会受到影响
  */
  /*
    公共方法调用
    如果有一个方法在你接手之前，别人在方法中写了2个变量 a,b
    我们在完成新的业务场景时，引申出了一个新的变量c，
    在不影响原来别人的业务代码的同时如何让这个公共方法修改后风险最小
    在设置c的时候将原本公共方法中的c的值，变为该形参的默认值
  */
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      error: false,
      offset: null // 请求下一页的页码，默认为null
      // totalCount: 0 // 总数据条数
    }
  },
  methods: {
    onReplyClick (event) {
      this.$emit('reply-click', event)
    },

    async onLoad () {
      console.log(this.targetId, 'targetId')
      try {
        // 1. 请求数据
        const { data: res } = await getCommentsAPI({
          type: this.type,
          source: this.targetId, // 源id
          offset: this.offset, // 页码
          limit: 10 // 每页数量
        })
        console.log(res)
        // 2. 将数据添加到列表中
        this.list.push(...res.data.results)
        // 更新中数据条数
        // this.totalCount = res.data.total_count
        this.$emit('update:totalCount', res.data.total_count)
        // 3. 将加载状态loading 改回false
        this.loading = false
        // 4. 判断是否有后续数据，对finished做一些操作
        if (res.data.results.length) {
          // 如果results中有值, 那么获取下一页的偏移量
          this.offset = res.data.last_id
        } else {
          // 如果results中没有数据了
          this.finished = true // 关闭加载
        }
      } catch (error) {
        console.log(error)
        this.error = true
        this.loading = false
      }
    },

    // 将新的评论添加到评论数组的最顶端
    unshiftComment (item) {
      this.list.unshift(item.new_obj)
    }
  }
}
</script>

