<template>
  <div>
    <van-icon
      :name="attitude === 1 ? 'good-job' : 'good-job-o'"
      :class="{collected: attitude === 1}"
      v-bind:loading="btnLoading"
      v-on:click="onCollect"
    />

  <!-- <van-button v-else class="follow-btn" round size="small" @click="onFollow" :loading="btnLoading">已关注 {{ isFollowed }}</van-button> -->

  </div>
</template>

<script>
import { addLikeAPI, deleteLikeAPI } from '../api/index.js'
export default {
  name: 'LikeArticle',
  // 使用的是v-model的形式来进行参数的传递
  props: {
    attitude: { // attitude -1: 无态度 0: 不喜欢 1：点赞
      type: Number,
      default: -1
    },
    articleId: { // article.art_id 文章id
      type: String,
      default: ''
    }
  },

  inject: {
    articleIdPro: {
      type: String,
      default: ''
    }
  },

  mounted () {
    console.log(this.articleIdPro, 'inject')
  },

  data () {
    return {
      btnLoading: false // 调用接口时的加载状态
    }
  },
  methods: {
    // 取消 、 收藏文章
    async onCollect () {
      this.btnLoading = true // 请求接口前开启
      let status = this.attitude // 当前点赞状态
      try {
        // 通过当前的用户关注状态来判断调用那个接口
        if (this.attitude === 1) {
          // 已点赞状态 - 要取消点赞
          status = -1
          await deleteLikeAPI(this.articleId)
        } else {
          // 未点赞状态 - 要开启点赞
          status = 1
          await addLikeAPI(this.articleId)
        }

        // 主动的将状态取反
        this.$emit('update:attitude', status)
      } catch (error) {
        this.$toast('操作失败，请重试')
      }
      setTimeout(() => {
        this.btnLoading = false // 请求执行后关闭
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.collected {
  color: red;
}
</style>
