<template>
  <van-cell class="comment-item" :title="comment.content">
    <!-- 用户头像 -->
    <template #icon>
      <van-image
        class="avatar"
        round
        fit="cover"
        :src="comment.aut_photo"
      />
    </template>

    <!-- 用户名 -->
    <template #title>
      <div class="user-name">{{ comment.aut_name }}</div>
    </template>

    <!-- 相对时间 -->
    <template #label>
      <p class="comment-content">{{ comment.content }}</p>
      <div class="bottom-info">
        <div class="publish-date">{{ comment.pubdate | relativeTime }}</div>
        <van-button class="reply-btn" round @click="$emit('reply-click', comment)">回复 {{comment.reply_count}}</van-button>
      </div>
    </template>

    <!-- 使用 right-icon 插槽来自定义右侧图标 -->
    <template #right-icon>
      <van-button
        class="like-btn"
        :class="{like: comment.is_liking}"
        :icon="comment.is_liking ? 'good-job' : 'good-job-o'"
        :loading="btnLoading"
        @click="onCommentLike"
      >
        {{ comment.like_count || '赞' }}
      </van-button>
    </template>
  </van-cell>
</template>

<script>
import { addCommentLikeAPI, deleteCommentLikeAPI } from '../../../api/index.js'
export default {
  name: 'CommentItem',
  props: {
    comment: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      btnLoading: false,
      commentClone: this.comment
    }
  },
  methods: {
    async onCommentLike () {
      this.btnLoading = true // 请求接口前开启
      try {
        // 通过当前的用户关注状态来判断调用那个接口
        if (this.comment.is_liking) {
          // 已点赞状态 - 要取消点赞
          await deleteCommentLikeAPI(this.comment.com_id)
          // 主动去增删点赞数量
          this.commentClone.like_count--
        } else {
          // 未点赞状态 - 要开启点赞
          await addCommentLikeAPI(this.comment.com_id)
          // 主动去增删点赞数量
          this.commentClone.like_count++
        }

        // 主动的将状态取反
        this.commentClone.is_liking = !this.comment.is_liking
      } catch (error) {
        this.$toast('操作失败，请重试')
      }
      setTimeout(() => {
        this.btnLoading = false // 请求执行后关闭
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.comment-item {
  .avatar {
    width: 70px;
    height: 70px;
    margin-right: 26px;
  }

  .user-name {
    color: #406599;
    font-size: 26px;
  }

  .comment-content {
    font-size: 32px;
    color: #222222;
    word-break: break-all;
    text-align: justify;
  }

  .bottom-info {
    display: flex;
    align-items: center;

    .publish-date {
      font-size: 20px;
      color: #222222;
      margin-right: 26px;
    }

    .reply-btn {
      width: 140px;
      height: 48px;
      font-size: 24px;
      color: #222222;
    }
  }

  .like-btn {
    border: none;
    height: 30px;
    padding: 0;
    font-size: 20px;
    margin-right: 10px;
  }

  .like {
    color: #e565e5;
  }
}
</style>
