<template>
  <div class="comment-reply">
    <!-- 头部导航 -->
    <van-nav-bar
      :title="comment.reply_count > 0 ? `${comment.reply_count}条回复` : '暂无回复' "
      left-arrow
      fixed
    >
      <template #left>
        <van-icon name="cross" @click="$emit('close')"/>
      </template>
    </van-nav-bar>

    <!-- 内容区域 -->
    <div class="scroll-wrap">
      <!-- 文章评论详情 -->
      <comment-item :comment="comment"></comment-item>

      <!-- 评论回复区域 -->
      <van-cell>全部回复</van-cell>

      <!-- 能否直接使用list列表组件， 可以1， 需要修改结构2  只需要修改数据的3 -->
      <comment-list
        ref="comment-list"
        :targetId="comment.com_id"
        type="c"
      ></comment-list>
    </div>

    <!-- 底部评论 -->
    <div class="reply-bottom">
      <van-button
        class="reply-btn"
        round
        size="small"
        @click="isPostShow = true"
      >
        写评论
      </van-button>
    </div>

    <!-- 回复评论 -->
    <van-popup v-model="isPostShow" position="bottom">
      <comment-post :targetId="comment.com_id" @post-success="onPostSuccess" :articleId="$route.params.articleId"></comment-post>
    </van-popup>
  </div>
</template>

<script>
import CommentItem from './comment-item.vue'
import CommentList from './comment-list.vue'
import CommentPost from './comment-post.vue'
export default {
  name: 'CommentReply',
  components: {
    CommentItem,
    CommentList,
    CommentPost
  },
  props: {
    comment: {
      type: Object,
      default: () => ({})
    }
  },

  data () {
    return {
      commentClone: this.comment,
      isPostShow: false // 写评论弹出层状态
    }
  },
  methods: {
    // 文章评论弹出层关闭操作
    onPostSuccess (item) {
      // 更新回复数量
      this.commentClone.reply_count++

      // 关闭写评论弹出层
      this.isPostShow = false

      // 通过ref去调用comment-list的方法将新的评价添加到列表的最顶端
      this.$refs['comment-list'].unshiftComment(item)
    }
  }
}
</script>

<style lang="less" scoped>
.comment-reply {
  .van-nav-bar {
    background-color: #ffffff;

    :deep(.van-nav-bar__title) {
      color: #000000;
    }
    .van-icon {
      color: #3296fa;
    }
  }

  .scroll-wrap {
    padding-top: 92px;
    padding-bottom: 88px;
    overflow-y: auto;
    background-color: #fff;
  }

  .reply-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 88px;
    border-top: 1px solid #d8d8d8;
    background-color: #fff;

    .reply-btn {
      width: 500px;
      height: 60px;
      font-size: 28px;
      line-height: 46px;
      border: 2px solid #eeeeee;
    }
  }
}
</style>
